import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue";
export default {
	data(){
		return {
			pageTitle: "",
			list: []
		}
	},
	components:{
		uniNavBar
	},
	onLoad(e) {
		this.pageTitle = e.title;
		this.getList();
	},
	methods:{
		back:function(){
			uni.navigateBack();
		},
		getList:function(){
			let that = this;
			uni.request({
				url:"/mock/mockData",
				success:function(res){
					console.log(res);
					that.list = res.testData;
				}
			})
		}
	}
}
